import { _decorator, Component, Node } from 'cc';
import { GameManger } from './GameManger';
const { ccclass, property } = _decorator;

@ccclass('EnemyBullet')
export class EnemyBullet extends Component {
    @property
    speed: number = 150;

    start() {
    }

    update(deltaTime: number) {
        let enemy = this.node.parent.getChildByName("enemy");
        let enemyY = enemy.position.y;
        let bulletY = this.node.position.y;
        bulletY -= this.speed * deltaTime;
        this.node.setPosition(this.node.position.x, bulletY, 0);
        if (bulletY < -550) {
            this.die();
        }
        if (bulletY >= enemyY) {
            this.die();
        }
    }
    die() {
        this.node.destroy();
    }
}

